.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  pointer-events: none;
}

.modal.enter-done {
  opacity: 1;
  pointer-events: visible;
}

.modal.exit {
  opacity: 0;
}

.modal-content {
  width: 500px;
  background-color: #220A65;
  transition: all 0.3s ease-in-out;
  transform: translateY(-200px);
}

.modal.enter-done .modal-content {
  transform: translateY(0);
}

.modal.exit .modal-content {
  transform: translateY(-200px);
}

.modal-header,
.modal-footer {
  padding: 20px;
}


.button:hover{
    color: white;
    background-color: red;
}

.button {

border: 1px solid rgba(255,255,255,0.5);
  border-radius: 50px;
  font-size: 1.2vw;
  font-style: normal;
  font-weight: 700;
  width: 15%;
  color: #6230BB;
  background-color: white;
  text-decoration: none;
  text-align: center;

  letter-spacing: 0px;
  font-style: normal;
  font-weight: 400;
  font-family: "AuroraMedium";
}

.modal-title {
  color: white;
  margin: 0;
}

.modal-body {
  padding: 30px;
  border-top: 1px solid #eee;
  border-bottom: 1px solid #eee;
}

